import React from 'react';
import "./Card.css";
import ProtTypes from 'prop-types';

export function Card({lang,img, fcolor, scolor}) {  

    return (
        <div className="card"
        style={{
            background:`linear-gradient( to left , ${fcolor} ,${scolor})`
        }}>
            <img src={img} alt={lang}/>
            <h3>{lang}</h3>
        </div>
    )
}

Card.ProtoTypes={
    lang: ProtTypes.string,
    img: ProtTypes.string,
    fcolor: ProtTypes.string,
    scolor:ProtTypes.string
}